document.addEventListener('DOMContentLoaded', () => {
  const menuBtn = document.querySelector('.mob-menu-btn');
  const closeBtn = document.querySelector('.menu-close-btn');
  const mobileMenu = document.querySelector('#mobileMenu');
  const body = document.body;

  menuBtn?.addEventListener('click', () => {
    mobileMenu.classList.add('is-open');
    body.classList.add('menu-open'); // –¥–ª—è –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ —Å–∫—Ä–æ–ª–ª–∞
  });

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é
  closeBtn?.addEventListener('click', closeMenu);

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –≤–Ω–µ –º–µ–Ω—é (–Ω–∞ —Ñ–æ–Ω)
  mobileMenu?.addEventListener('click', e => {
    if (e.target === mobileMenu) {
      closeMenu();
    }
  });

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ Escape
  document.addEventListener('keydown', e => {
    if (e.key === 'Escape' && mobileMenu.classList.contains('is-open')) {
      closeMenu();
    }
  });

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ —Å—Å—ã–ª–∫—É (–æ—Å–æ–±–µ–Ω–Ω–æ —É–¥–æ–±–Ω–æ –¥–ª—è –æ–¥–Ω–æ—Å—Ç—Ä–∞–Ω–∏—á–Ω–∏–∫–∞)
  document.querySelectorAll('.mobile-link').forEach(link => {
    link.addEventListener('click', closeMenu);
  });

  function closeMenu() {
    mobileMenu.classList.remove('is-open');
    body.classList.remove('menu-open');
  }

  // –û–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ: —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Ö–µ–¥–µ—Ä + —Å–º–µ–Ω–∞ —Å—Ç–∏–ª—è –ø—Ä–∏ —Å–∫—Ä–æ–ª–ª–µ
  let lastScroll = 0;
  window.addEventListener('scroll', () => {
    const currentScroll = window.scrollY;

    if (currentScroll > 50) {
      document.querySelector('.header').classList.add('scrolled');
    } else {
      document.querySelector('.header').classList.remove('scrolled');
    }

    lastScroll = currentScroll;
  });
});
